import React from "react";
import { graphql, Link } from "gatsby";

import Layout from "../components/layout";
import SEO from "../components/seo";

export default function BlogPage({
  data: {
    allMarkdownRemark: { edges },
  },
}) {
  const posts = edges
    .map((edge) => edge?.node?.frontmatter)
    .filter((edge) => edge != null);
  const postByLog = posts.reduce((acc, value) => {
    if (!acc[value.category]) {
      acc[value.category] = [];
    }

    acc[value.category].push(value);

    return acc;
  }, {});

  return (
    <Layout currentPage="Maria" currentSubPage="Blog">
      <SEO title="Blog" />
      <h2>Engineering Log</h2>
      {postByLog["engineer"]?.map((post, index) => (
        <div key={"personal" + index} className="lcars-listing">
          <Link to={post.slug}>
            <div className="lcars-listing-inner">
              <div>{post.title}</div>
              <div>{post.date}</div>
            </div>
          </Link>
        </div>
      ))}
      <h2>Personal Log</h2>
      {postByLog["personal"]?.map((post, index) => (
        <div key={"personal" + index} className="lcars-listing">
          <Link to={post.slug}>
            <div className="lcars-listing-inner">
              <div>{post.title}</div>
              <div>{post.date}</div>
            </div>
          </Link>
        </div>
      ))}
    </Layout>
  );
}

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            slug
            title
            category
          }
        }
      }
    }
  }
`;
